import styled from "styled-components";
import NavBar from './NavBar';
import Profile from './Profile';
const StyledContainer = styled.div`
  background-color: white;
  grid-column: 4 / 19;
  borer-radius: 15px;
`;

const Container = () => (
  <StyledContainer>
      <NavBar />
      <Profile />
  </StyledContainer>
);

export default Container;
